import React from 'react';

import Status from './Status';

import './Bar.css';

export default class Bar extends React.Component {
  state = {
    width: "9",
    height: "9",
    bombsCount: "10",
    expanded: false,
  }

  handleRestartClick = e => {
    const { width, height, bombsCount } = this.state;
    this.props.onRestartClick({ width, height, bombsCount });

    this.setState({ expanded: false });
  }

  handleOptionsClick = e => {
    this.setState({ expanded: !this.state.expanded });
  }

  handleInputChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  }

  render() {
    let message = '';

    switch (this.props.status) {
      case Status.win:
        message = '–ü–æ–±–µ–¥–∞!';
        break;
      case Status.lose:
        message = '–ò–≥—Ä–∞ –æ–∫–æ–Ω—á–µ–Ω–∞';
        break;
      default:
        break;
    }

    const expandable = 'expandable' + (this.state.expanded ? ' open' : '')

    return (
      <div className="bar">
        <div className="main">
          <div className="main-item button" onClick={this.handleRestartClick}>‚Üª</div>
          <div className="main-item message">{message}</div>
          <div className="main-item button expander" onClick={this.handleOptionsClick}>‚öô</div>
        </div>
        <div className={expandable}>
          <label>–®–∏—Ä–∏–Ω–∞ –ø–æ–ª—è</label>
          <input type="text" name="width" value={this.state.width} onChange={this.handleInputChange}/>
          <br/>
          <label>–í—ã—Å–æ—Ç–∞ –ø–æ–ª—è</label>
          <input type="text" name="height" value={this.state.height} onChange={this.handleInputChange}/>
          <br/>
          <label>–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –±–æ–º–±</label>
          <input type="text" name="bombsCount" value={this.state.bombsCount} onChange={this.handleInputChange}/>
        </div>
      </div>
    );
  }
}
